import { Component, Host, Prop, h } from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';
import { TP_DOWNLOAD_BUTTON } from './utils/download-button.constant';
import { TpDownloadButtonSizes } from './utils/download-button.type';

@Component({
  tag: 'tp-download-button',
  styleUrl: 'download-button.scss',
  shadow: true,
  assetsDirs: ['assets'],
})
export class TpDownloadButton {
  /**
   * Define a variação de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor;

  /**
   * Define o progresso do download.
   */
  @Prop({ reflect: true }) value = 0;

  /**
   * Define se o download está ou não em progresso.
   */
  @Prop({ reflect: true }) downloading = false;

  /**
   * Define se o download está finalizado.
   */
  @Prop({ reflect: true }) downloaded = false;

  /**
   * Define a variação de tamanho do componente.
   */
  @Prop({ reflect: true }) size: TpDownloadButtonSizes;

  render() {
    const { color, value, downloading, downloaded, size } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-download-button': true,
          'tp-download-button--downloading': downloading && !downloaded,
          'tp-download-button--downloaded': downloaded,
          [`tp-download-button--${size}`]: Boolean(size),
        })}
      >
        <div class="tp-download-button__circles-container">
          <svg viewBox="0 0 36 36">
            <circle cx="18" cy="18" r="16" class="tp-download-button__circle" />
            <circle
              cx="18"
              cy="18"
              r="16"
              class="tp-download-button__circle tp-download-button__circle--value"
              style={{ '--value': `${value}` }}
            />
          </svg>
        </div>
        <ion-icon
          class="tp-download-button__arrow-icon"
          src={TP_DOWNLOAD_BUTTON.icons.arrowDown}
        ></ion-icon>
        <div class="tp-download-button__square-icon"></div>
      </Host>
    );
  }
}
